/* ARview.css */
.slider-container {
  position: absolute;
  bottom: 100%; 
  left: 50%; 
  transform: translateX(-50%); 
  z-index: 1000;
  margin-bottom: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  }

  .button-container{
    position: absolute;
    bottom: 20px;
    left: 30px;
    z-index: 1000;
    display: flex;
  }

  .button-container-2{
    position: absolute;
    bottom: 20px;
    right: 30px;
    z-index: 1000;
    display: flex;
  }
  
  .slider-container input[type="range"] {
    -webkit-appearance: slider-vertical; /* Safari y Chrome */
    appearance: slider-vertical; /* Hace el slider vertical */
    width: 8px; /* Grosor del slider */
    height: 150px; /* Altura del slider */
    background: #ddd;
    outline: none;
    border-radius: 5px;
    writing-mode: bt-lr;
  }
  
  
  .slider-container p {
    color: white; /* Texto en blanco */
    margin-top: 10px;
    width: 40px; 
  text-align: center; /* Centrar el texto */
  }




  /* Estilos para el botón redondo de sonido */
.sound-button {
    background-color: white; /* Color verde para el botón */
    border: none;
    border-radius: 50%; /* Hace que el botón sea redondo */
    width: 60px;  /* Tamaño del botón */
    height: 60px; /* Tamaño del botón */
    color: #070062; /* Color del ícono */
    font-size: 24px; /* Tamaño del ícono */
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.2); /* Sombra para darle profundidad */
    transition: background-color 0.3s ease; /* Transición suave de color */
  }
  
